.About-card{display:flex;height:auto;align-items:center;justify-content:space-between;margin-top:5rem}.About-card .image{width:40%;padding:1rem 1rem}.About-card .image img{width:100%}.About-card .text{width:60%}

.my-card{width:100%;border-radius:0%;background:#EAD7D1;border-radius:.2rem;overflow:hidden}.my-card .buy-now-btn{width:35%;height:3rem;background-color:#1F1A38;display:flex;align-items:center;justify-content:space-between;justify-content:center;border-radius:.2rem;text-decoration:none;font-size:1rem;letter-spacing:0.1rem;color:#d9b}.my-card-body{display:flex;align-items:center;justify-content:space-between}.hvr-sweep-to-right{display:inline-block;vertical-align:middle;transform:perspective(1px) translateZ(0);box-shadow:0 0 1px rgba(0,0,0,0);position:relative;transition-property:color;transition-duration:0.3s;border-radius:.2rem}.hvr-sweep-to-right:before{content:"";position:absolute;z-index:-1;top:0;left:0;right:0;bottom:0;background:#d9b;transform:scaleX(0);transform-origin:0 50%;transition-property:transform;transition-duration:0.3s;transition-timing-function:ease-out;border-radius:.2rem}.hvr-sweep-to-right:hover,.hvr-sweep-to-right:focus,.hvr-sweep-to-right:active{color:white}.hvr-sweep-to-right:hover:before,.hvr-sweep-to-right:focus:before,.hvr-sweep-to-right:active:before{transform:scaleX(1)}

.UnderLineHeading{background:transparent;padding-top:2rem !important;display:flex;align-items:center;justify-content:space-between}.UnderLineHeading .UnderLine-Top{height:1px;width:30%;background-color:#1F1A38}.UnderLineHeading .UnderLine-Bottom{height:1px;width:30%;background-color:#1F1A38}.UnderLineHeading h1{width:35%;text-align:center;padding-top:0.2rem;font-size:2rem;letter-spacing:0.3rem}

.Collections .Health-collection{display:grid;grid-template-columns:auto auto auto auto;margin-top:3rem;margin-bottom:3rem;align-items:center;justify-content:space-evenly;grid-row-gap:10px;row-gap:10px;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}

.Home{background:pink;height:auto;width:100vw}#img{width:100%}.HomeInfoCard{display:grid;grid-template-columns:auto auto auto auto;margin-top:3rem;margin-bottom:3rem;align-items:center;justify-content:space-evenly;grid-row-gap:10px;row-gap:10px;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}

