r_2 .searchBarBox .searchBar input[type=text]{width:5rem;border:none;outline-width:0ch;background:transparent;color:#1F1A38;padding-left:1rem}.NavBar_2 .searchBarBox .searchBar img{width:1.3rem;padding:0.4rem 0rem;cursor:pointer}.NavBar_2 .searchBarBox #signIn{background:#d9b;color:#EAD7D1;letter-spacing:0.1rem;border:none;transition:0.5s linear !important}

